'use client';

import Image from 'next/image';
import { useEffect, useState } from 'react';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';

type Slide = {
  id: string | number;
  imageSrc: string;
  imageAlt?: string;
  title?: string;
  blurb?: string;
  ctaLabel?: string;
  ctaHref?: string;
};

type Props = {
  slides: Slide[];
  autoPlayMs?: number;
};

export default function HeroCarousel({ slides, autoPlayMs = 6000 }: Props) {
  const [index, setIndex] = useState(0);
  const count = slides.length;

  useEffect(() => {
    const t = setInterval(() => setIndex((i) => (i + 1) % count), autoPlayMs);
    return () => clearInterval(t);
  }, [count, autoPlayMs]);

  const goTo = (i: number) => setIndex((i + count) % count);
  const slide = slides[index];

  return (
    <section className="w-full overflow-hidden">
      {/* IMAGE SECTION */}
      <div className="relative aspect-[16/9] w-full md:aspect-[21/9] lg:aspect-[24/9]">
        <AnimatePresence mode="wait">
          <motion.div
            key={slide.id}
            initial={{ opacity: 0, scale: 1.02 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.98 }}
            transition={{ duration: 0.8 }}
            className="absolute inset-0"
          >
            <Image
              src={slide.imageSrc}
              alt={slide.imageAlt ?? ''}
              fill
              priority
              sizes="100vw"
              className="object-cover"
            />
          </motion.div>
        </AnimatePresence>
      </div>

      {/* TEXT + CTA SECTION */}
      <div className="flex flex-col items-center justify-center bg-white py-10 text-center">
        <AnimatePresence mode="wait">
          <motion.div
            key={slide.id + '-text'}
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -30 }}
            transition={{ duration: 0.6 }}
            className="max-w-2xl px-6"
          >
            {slide.title && (
              <h1 className="text-3xl font-extrabold uppercase md:text-5xl">
                {slide.title}
              </h1>
            )}
            {slide.blurb && (
              <p className="mt-3 text-base text-neutral-700 md:text-lg">
                {slide.blurb}
              </p>
            )}
            {slide.ctaHref && (
              <Link
                href={slide.ctaHref}
                className="mt-6 inline-block rounded-full bg-black px-6 py-3 text-white transition hover:bg-neutral-800"
              >
                {slide.ctaLabel}
              </Link>
            )}
          </motion.div>
        </AnimatePresence>

        {/* BULLETS */}
        <div className="mt-8 flex items-center justify-center gap-3">
          {slides.map((s, i) => (
            <button
              key={s.id}
              onClick={() => goTo(i)}
              aria-label={`Go to slide ${i + 1}`}
              className={`h-2.5 rounded-full transition-all ${
                i === index
                  ? 'w-8 bg-neutral-800'
                  : 'w-2.5 bg-neutral-300 hover:bg-neutral-500'
              }`}
            />
          ))}
        </div>
      </div>
    </section>
  );
}
