'use client';

import { Search, User, ShoppingBag, Menu, X } from 'lucide-react';
import { useState } from 'react';
import Image from 'next/image';
import NextLink from 'next/link';

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [cartCount] = useState(2);

  const navLinks = [
    { name: 'Новинки', href: '#new' },
    { name: 'Каталог', href: '#women' },
    { name: 'Скидки', href: '#sale' },
    { name: 'Контакты', href: '#collections' },
  ];

  return (
    <header className="sticky top-0 z-50 bg-white border-b border-gray-200">
      {/* Top Banner */}
      <div className="bg-black text-white text-center py-2 px-4">
        <p className="text-sm">Скидки на первую покупку 10% | Покупайте сейчас</p>
      </div>

      {/* Main Header */}
      {/* Expanded container: no fixed 1440 cap; grows nicely on large screens */}
      <div className="mx-auto w-full px-4 sm:px-6 lg:px-8">
        <div className="mx-auto w-full max-w-7xl xl:max-w-[1800px] 2xl:max-w-[2000px] grid grid-cols-3 items-center h-16 lg:h-20">
          {/* Left: Mobile menu button + Desktop nav */}
          <div className="flex items-center gap-4 justify-self-start">
            {/* Mobile Menu Button */}
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="lg:hidden p-2 -ml-2"
              aria-label="Toggle menu"
              aria-expanded={isMenuOpen}
            >
              {isMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
            </button>

            {/* Desktop Navigation */}
            <nav className="hidden lg:flex items-center gap-8">
              {navLinks.map((link) => (
                <NextLink
                  key={link.name}
                  href={link.href}
                  className="text-sm font-medium text-gray-700 hover:text-black transition-colors"
                >
                  {link.name}
                </NextLink>
              ))}
            </nav>
          </div>

          {/* Center: Logo */}
          <div className="justify-self-center">
            <NextLink href="/" aria-label="Serein home" className="inline-block">
              <Image
                src="/logo_purple.svg"
                alt="NelyLook"
                width={240}
                height={60}
                priority
                className="h-10 w-auto md:h-14 lg:h-16"
              />
            </NextLink>
          </div>

          {/* Right: Icons */}
          <div className="flex items-center justify-self-end gap-4">
            <button
              className="p-2 hover:bg-gray-100 rounded-full transition-colors"
              aria-label="Search"
            >
              <Search className="w-5 h-5" />
            </button>
            <button
              className="p-2 hover:bg-gray-100 rounded-full transition-colors"
              aria-label="Account"
            >
              <User className="w-5 h-5" />
            </button>
            <button
              className="p-2 hover:bg-gray-100 rounded-full transition-colors relative"
              aria-label="Shopping bag"
            >
              <ShoppingBag className="w-5 h-5" />
              {cartCount > 0 && (
                <span className="absolute -top-1 -right-1 w-5 h-5 bg-black text-white text-xs flex items-center justify-center rounded-full">
                  {cartCount}
                </span>
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Navigation */}
      {isMenuOpen && (
        <div className="lg:hidden border-t border-gray-200 bg-white">
          <nav className="px-4 py-4 space-y-3">
            {navLinks.map((link) => (
              <NextLink
                key={link.name}
                href={link.href}
                className="block text-base font-medium text-gray-700 hover:text-black transition-colors py-2"
                onClick={() => setIsMenuOpen(false)}
              >
                {link.name}
              </NextLink>
            ))}
          </nav>
        </div>
      )}
    </header>
  );
}
